import * as ActionTypes from './actionTypes'

export const Users = (state = {
  isLoading: false,
  isAuthenticated: localStorage.getItem('token') ? true : false,
  user: localStorage.getItem('creds') ? JSON.stringify(localStorage.getItem('creds')) : null,
  token: localStorage.getItem('token'),
  errMess: null
}, action) => {
  switch (action.type) {
  case ActionTypes.LOGIN_REQUEST:
    return { ...state, isLoading: true, isAuthenticated: false, user: action.data }
  case ActionTypes.SUCCESS_LOGIN:
    return { ...state, isLoading: false, isAuthenticated: true, token: action.data, errMess: null }
  case ActionTypes.LOGIN_FAILED:
    return { ...state, isLoading: false, isAuthenticated: false, errMess: action.data }
  case ActionTypes.REQUEST_REGISTER:
    return { ...state, isLoading: true, isAuthenticated: false, errMess: null }
  case ActionTypes.REGISTER_SUCCESS:
    return { ...state, isLoading: false, isAuthenticated: true, token: action.data, errMess: null }
  case ActionTypes.REGISTER_FAILED:
    return { ...state, isLoading: false, isAuthenticated: false, errMess: action.data }
  case ActionTypes.LOGOUT_REQUEST:
    return { ...state, isLoading: true, isAuthenticated: true }
  case ActionTypes.LOGOUT_SUCCESS:
    return { ...state, isLoading: false, isAuthenticated: false, token: null, user: null }
  case ActionTypes.LOGOUT_FAILED:
    return { ...state, errMess: action.data }
  default:
    return state
  }
}